@import "../../global.scss";

.portfolio {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 50vh;

  h1 {
    font-size: 50px;
  }

  ul {
    margin: 10px;
    list-style: none;
    display: flex;
    align-items: center;
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 100px;

    .item {
      width: 500px;
      height: 320px;
      border-radius: 20px;
      border: 1px solid rgb(240, 239, 239);
      margin: 20px 10px 20px 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      cursor: pointer;

      h3 {
        position: absolute;
        font-size: 30px;
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: black;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }

  .a {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100px;
    height: 100px;
    bottom: -200px;

    img {
      left: -50%;
      width: 50px;
      height: 50px;
      animation: arrowBlink 2s infinite;
    }

    @keyframes arrowBlink {
      100% {
        opacity: 0;
      }
    }
  }
}
.boxloader {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50vh;

  .loader {
    height: 200px;
  }
}
